<template>
    <header :class="`nav-bar ${ hairline ? 'Hairline Hairline_bottom' : '' }`">
        <i
            :class="[customBack?'nav-bar_backWhite':'nav-bar__back']"
            @click="backHandler" />
        <h1
            class="nav-bar__title"
            :style="customTitleStyle">{{ title }}</h1>
        <div class="nav-bar__info"><slot /></div>
    </header>
</template>
<script>
import { quitCurrentWebview } from '@/utils/jsApiV2/client';

export default {
    name: 'NavBar',
    props: {
        title: {
            type: String,
            default: '',
        },
        hairline: {
            type: Boolean,
            default: true,
        },
        finishExecute: {
            type: Boolean,
            default: false,
        },
        clickBackHandler: {
            type: Function,
            default: null,
        },
        webview: {
            type: Boolean,
            default: true,
        },
        customBack: {
            type: Boolean,
            default: false,
        },
        customTitleStyle: {
            type: Object,
            default: () => ({}),
        },
    },
    methods: {
        backHandler() {
            if (this.clickBackHandler) {
                this.clickBackHandler();
                // 全部逻辑交由父组件处理 不再执行后续逻辑
                if (this.finishExecute) {
                    return;
                }
            }

            if (this.webview) {
                quitCurrentWebview();
            } else {
                this.$router.go(-1);
            }
        },
    },
};
</script>
