<template>
    <div class="container">
        <nav-bar title="系统升级"/>
        <div class="content">
            <div>
                <div class="img"/>
                <p class="title">{{ tips.title }}</p>
                <p class="tip">{{ tips.tip }}</p>
                <p
                    v-for="(item, index) in tips.content"
                    :key="index">
                    {{ item }}
                </p>
                <!-- <p>升级时间：2019年11月28日 15:00 ~ 2019年11月28日 17:00</p> -->
            </div>
        </div>
    </div>
</template>

<script>
import NavBar from '@/components/NavBar';
import { getConfigByKey } from '@/api/common';
import { apolloUpdateTip } from './config';

export default {
    name: 'UpdateTip',
    components: {
        NavBar,
    },
    data() {
        return {
            tips: {
                title: '提现服务升级',
                tip: '想看正在升级提现服务，升级前金币正常发放！给您带来的不便，还请谅解！',
                content: [
                    '升级后，提现折扣力度更大！',
                    '升级时间：2019年11月28日 15:00 ~ 2019年11月28日 17:00',
                ],
            },
        };
    },
    methods: {
        getTip() {
            return getConfigByKey({
                key: apolloUpdateTip,
            }).then((res) => {
                this.tips = res.data;
            });
        },
    },
    created() {
        this.getTip().finally(() => {
            this.$root.hidePlaceholder();
        });
    },
    mounted() {
        // eslint-disable-next-line func-names
        // this.$nextTick(function () {
        //     this.$root.hidePlaceholder();
        // });
    },
};
</script>

